@font-face {
  font-family: "MapoPeacefull";
  src: url("./fonts/MapoPeacefull_ttf.ttf") format('truetype');
}
@font-face {
  font-family: "Binggrae-Bold";
  src: url("./fonts/Binggrae-Bold_ttf.ttf") format('truetype');
}



.App {

  margin: 0;
  padding:0;
  
}
a {
  text-decoration: none;
  font-family: "Binggrae-Bold";}
body{ /*파란 배경(이었던 것) #8ECFFF*/
  background-color: #FFF38B;
  background-attachment: fixed;
  background-size: 250% 100%;
  width:100vw;
  
}
.back2{ /*노란배경*/
  margin: auto;
  background-color: #FFF38B;
  width : 100vw;
  height: 100vh;
  overflow: hidden;
 /* margin: 0 auto;*/
}
.star1{ /*오른쪽 별빛*/
  position: relative;
  left: 80vw;
  top: 5vh;
}
.star2{ /*왼쪽 별빛*/
  position: relative;
  left: 20vw;
  bottom: 2vh;
}
.back1{ /*하얀 box*/
  position: relative;
  background-color : white;
  border-radius : 20px;
  display: flex;
  height: 58vh;
}
.stroke{ /*핑크 테두리*/
  display: flex;
  margin: auto;
  width: 90vw;
  height: 55vh;
  border-radius : 20px;
  box-sizing : border-box;
  border : 6px solid #F2769B;
}
.vertical{ /*핑크 테두리안 가운데정렬*/
  margin: auto auto;
}
.AlignCenter{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 85px;
   /*header 가운데 정렬*/}

.AlignCenterFooter{ /*footer 가운데 정렬*/
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  padding: 30px 0px;
  position:relative;
  bottom: 70px;

}
.Button{
  display: block;
  margin: 20px auto;
  color: white;
  font-size : 180%;
  background-color: #F2769B;
  height: 50px;
  width: fit-content;
  border-radius: 30px;
  text-align: center;
  overflow: hidden;
  font-family: "Binggrae-Bold";
  border: none;
}
.Text{ /*내나이는 Text*/
  color: Black;
  text-align: center;
  margin: 0px;
  font-family: "Binggrae-Bold";
  font-size: 200%;
  padding: 20px 0 20px 0;
  word-break: keep-all;
}
.TextQ{ /*당신의 나이는?*/
    color: #DAAA00;
    text-align: center;
    margin: 0px;
    font-family: "Binggrae-Bold";
    font-size: 170%; 
    padding: 50px 10px 30px 10px;
}

input {
  width: 50px;
  height: 30px;
  font-size: xx-large;
  border-radius: 5px;
}

.Memory{
  width : 350px;
  height : 200px;
}
.Header {
  position: absolute;
  top : 20px;
  width: 60px;
  height: 85px;
}
.Footer {
  width : 150px;
  height : 35px;
}
.byeImg{
  width: 75px;
  height: 150px;

}
.TextB{
  color: Black;
  text-align: center;
  margin: 0px;
  font-family: "Binggrae-Bold";
  font-size: 110%;
  word-break: keep-all;
  white-space: pre-wrap;
}
.AlignCenterB{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
  /*Bye 페이지 가운데 정렬*/
}
.MemoryBack{  /*추억사진 배경*/
  background-color:white;
  display : flex;
  justify-items: center;
  align-items: center;
  width : fit-content;
  height: fit-content;
  border-radius: 30px;
}
.Hi1{  /*오른쪽 Hi1*/
  position: relative;
  left: 60vw;
  top: 2vh;
}
.Hi2{ /*왼쪽 Hi2*/
  position: relative;
  bottom: 10vh;
}

