@font-face {
  font-family: "MapoPeacefull";
  src: url("./fonts/MapoPeacefull_ttf.ttf") format('truetype');
}
@font-face {
  font-family: "Binggrae-Bold";
  src: url("./fonts/Binggrae-Bold_ttf.ttf") format('truetype');
}


.cloud1{
  position: relative;
  left: 70vw;
  top: 2vh;
  overflow-x: hidden;
}
.cloud2{
  position: relative;
  bottom:7vh;
  right: 10vw;
}

.back1{
  background-color: #FFF38B;
  margin: auto;
  width : 100vw;
  height: 100vh;
}
/*.back2{
  <div className={style.back2}>
  border-color:#F2769B;
  border-width: 6px;
  border-style: solid none;
}*/

.Text{
    font-weight : 400;
    font-family: "MapoPeacefull";
    color: rgb(13, 53, 6);
    font-size: 20px;
    text-align: center;
    word-break: keep-all;
  }
p {
  margin: 10px 10px;
  font-family: "MapoPeacefull";
}
span {
  font-weight: bold;
  font-family: "MapoPeacefull";
}
.Button{
  display: block;
  margin: 20px auto;
  color: white;
  font-size : 180%;
  background-color: #F2769B;
  height: 50px;
  width: fit-content;
  border-radius: 30px;
  text-align: center;
  overflow: hidden;
  border: none;
}
  .Letter{
    width : 190px;
    height : 190px;
    margin-left:5px;
    margin-right:5px;
    margin-bottom:10px;
    border-radius : 12px;
  }

  .AlignLeft{
    float:left;
  }
  .unisImage {
    width: 100%;
    height: auto;
  }
  .wrapper {
    position: relative;
    padding-top: 56.25%;
  }
  .unisVideo {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
  }
  .AlignCenter{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top : 10px;
  }
  a{
    font-family:"Binggrae-Bold";
    text-decoration: none;
    color: white;
  }
  .FooterB {
    width : 150px;
    height : 35px;
    padding: 20px 0;
  }
  .Arrow{
    height : 2.5vw;
  }
  /*.space{
    min-height: 28vh;
  }*/

  @media (min-width: 1023px) {
    body{
      width:960px;
      margin: 0 auto;
    }
    .back1{
      width: 960px;
    }
    .Header {
      position: absolute;
      top: 40px;
      width: 90px;
      height: 180px;
    }
    .Text {font-size: 200%;}
    input {
      width: 100px;
      height: 50px;
      font-size: xx-large;
    }
    .Footer {
      width : 225px;
      height : 50px;
      padding: 20px 0;
    }
    .Letter{
      width : 25vw;
      height : 25vw;
      margin-left:5px;
      margin-right:5px;
      margin-bottom:10px;
      border-radius : 12px;
    }
    .Button{
      display: block;
      margin: 20px auto;
      color: white;
      font-size : 220%;
      background-color: #F2769B;
      height: 50px;
      width: fit-content;
      border-radius: 30px;
      text-align: center;
      overflow: hidden;
      border: none;
    }
    header,p{
      margin-left: 20px;
      margin-top: 35px;
      margin-bottom: 35px;
      line-height: 40px;
    }

    .cloud1{
      position: relative;
      left: 65vw;
      top: 2vh;
      overflow-x: hidden;
    }
    .cloud2{
      position: relative;
      bottom:7vh;
      right: 0;
      left: 23vw;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    .Letter{
      width : 20vw;
      height : 20vw;
      margin-left:5px;
      margin-right:5px;
      margin-bottom:10px;
      border-radius : 12px;
    }
    .cloud2{
      position: relative;
      bottom:7vh;
      right: 0;
      left: 10vw;
    }
    }